










import Vue from 'vue'

export default Vue.extend({
  model: {
    prop: 'value',
    event: 'input-value'
  },
  props: {
    value: {
      type: [String, Number],
      default: ''
    }
  },
  computed: {
    listeners (): object {
      return {
        ...this.$listeners,
        'input': (event: any) => {
          this.$emit('input', event)
          this.$emit('input-value', event.target.value)
        }
      }
    }
  }
})
