




















import Vue from 'vue'

export default Vue.extend({
  props: {
    routeName: {
      type: String,
      required: true
    },
    currentPage: {
      type: Number,
      required: true
    },
    totalPages: {
      type: Number,
      required: true
    },
    showPagesStats: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    previousPage (): number {
      return this.currentPage - 1
    },
    nextPage (): number {
      return this.currentPage + 1
    },
    commonQuery (): any {
      return this.$route.name === this.routeName
        ? this.$route.query
        : {}
    },
    previousPageQuery (): any {
      return {
        ...this.commonQuery,
        page: this.previousPage
      }
    },
    nextPageQuery (): any {
      return {
        ...this.commonQuery,
        page: this.nextPage
      }
    },
    previousPageExist (): boolean {
      return this.currentPage > 1
    },
    nextPageExist (): boolean {
      return this.currentPage < this.totalPages
    },
    totalPagesToDisplay (): string | number {
      return this.totalPages === Number.POSITIVE_INFINITY
        ? ''
        : this.totalPages
    }
  }
})
