






























import Vue from 'vue'
import { faStar } from '@fortawesome/free-solid-svg-icons'

export default Vue.extend({
  data () {
    return {
      starIcon: faStar
    }
  },
  props: {
    rating: {
      type: Number,
      required: true
    },
    votesCount: {
      type: Number,
      required: true
    }
  },
  computed: {
    ratingExist (): boolean {
      return !!this.rating
    }
  }
})
