














import Vue from 'vue'
import { faHeart } from '@fortawesome/free-solid-svg-icons'

export default Vue.extend({
  props: {
    active: {
      type: Boolean,
      default: false
    },
    icon: {
      type: Object,
      default: () => faHeart
    },
    size: {
      type: String,
      default: 'medium',
      validator (size: string) {
        return [
          'medium',
          'large'
        ].includes(size)
      }
    },
    color: {
      type: String,
      default: 'dark',
      validator (color: string) {
        return [
          'dark',
          'light'
        ].includes(color)
      }
    }
  }
})
